.root {
    margin-top: 38px;
    margin-bottom: 60px;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.leftBlock {
    display: flex;
    align-items: center;
}

.logo {
    width: 76px;
    flex-grow: 0;
    flex-shrink: 0;
}

.title {
    margin: 0;
    margin-left: 24px;
    font-size: 24px;
    line-height: 29px;
    text-transform: capitalize;
    font-weight: normal;
    letter-spacing: 1.2px;
}

.linkToHome,
.linkToHome:hover {
    color: inherit;
    text-decoration: none;
}

.rightBlock {
    flex-shrink: 0;
    padding-left: 10px;
}

.rightLink {
    padding: 4px 5px;
    display: block;
}

.teamLink {
    padding: 2px 7px;
    display: block;
}

.logoutButton.logoutButton {
    font-size: 16px;
    line-height: 1.8;
    text-transform: none;
}


@media (max-width: 640px) {
    .root {
        margin-top: 18px;
        margin-bottom: 25px;
    }
    .logo {
        width: 51px;
    }
    .title {
        margin-left: 18px;
        font-size: 18px;
        line-height: 19px;
    }
}
