:root {
  --background: #333;
  --text: #fff;
  --additional-text: rgba(255, 255, 255, 0.5);
  --link-color: #00E5D2;
  
  --container-size: 1024px;
}

body {
  min-width: 320px;
  margin: 0;
  background-color: var(--background);
  color: var(--text);
  font-size: 24px;
  line-height: 1.33;
  font-family: "proxima-nova", "Proxima Nova", "Roboto", "Helvetica", "Arial", sans-serif;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


a {
  color: var(--link-color);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.container {
  max-width: 825px;
  margin: 20px auto;
  padding: 0 32px 0 24px;
  box-sizing: border-box;
}

@media (max-width: 640px) {
  .container {
    margin: 10px auto;
  }
}
